import React from 'react';
import { RouteProps } from 'react-router';
import { Page, ExternalLink, CodeSample, ArticleTemplate, ArticleText } from '_components';
import { ARTICLES } from 'articles';

const AnimatingReactRouterRoute: React.FC<RouteProps> = (props) => {

  const article = ARTICLES.ANIMATING_REACT_ROUTER;

  return (
    <Page className="AnimatingReactRouterRoute">

      <ArticleTemplate article={article}>

        <ArticleText>
          <p className="mxn__fontLeading">Animating between routes using react-router, for me, has always been a pain in the backside. The awful react-transition-group documentation, coupled with out-of-date blog posts and difficulty in debugging issues, at times left me wanting to take a hammer to my monitor.</p>
          <p>I've not liked some of the existing solutions because they use absolute positioning to achieve cross-fades between routes (which have caused more headaches e.g. FOUC), or they do not cater for the fact that content may be lazy loaded. I'm not proclaiming that my solution trumps all, but it has worked for me in both personal and client projects.</p>
          <p>This post assumes that you are:</p>
          <ul className="bulletList">
            <li>using a recent version of react (16.10.2), react-router (5.1.2) and react-transition-group (4.3.0) - bracketed versions are what I used at the time of writing this article</li>
            <li>lazy-loading your route components (although this method will work without lazy-loading)</li>
            <li>are not looking to cross-fade your route transitions and fading out the first route before animating in the next one</li>
            <li>building your app in TypeScript - if not, it's straightforward enough to convert into regular JS</li>
            <li>using a browser that wasn't built in the 18th century</li>
          </ul>
          <h2>App.tsx</h2>
          <p>In your App.tsx or similar entry point for your routes, enclose your Switch component inside the TransitionGroup and CSSTransition components like so:</p>
          <CodeSample language="tsx" code={codeSampleApp} />
          <p>Note that we have set the timeout prop, which is defining how long we want the 'enter' and 'exit' animations to take. In this case, entering will take 1s long and exiting 0.2s. This will need to match up to the animation-duration that you will define in your SCSS later.</p>
          <h2>Helpers.scss</h2>
          <p>I usually keep <strong>global</strong> styles in a root-level styles folder (component styles live alongside the component). This contains basic site setup, SCSS variables, mixins, and utility/helper classes. I also like to prefix any animation utility classes with 'a-'.</p>
          <CodeSample language="scss" code={codeSampleStyles} />
          <p>The reason why animation is used over transition is threefold:</p>
          <ul>
            <li>Transitions do not execute if the class is already applied when the component mounts. Animations, on the other hand, run perfectly even if the class is already applied at mount time.</li>
            <li>The transitions could have already started whilst the route component is being lazy-loaded- we want to delay executing the transition until the route has been fetched.</li>
            <li>the CSSTransition component will apply the fade-enter and fade-enter-active classes to the Suspense fallback component instead of the Route component (because the fallback component will be shown whilst the route component is being fetched).</li>
          </ul>
          <p>Finally, you can see the fade-in animation takes 1s in total to complete (0.75s transition + 0.25s delay). We delay by 0.25s to give the exit animation (0.2s duration) enough time to complete, before we start our entry animation.</p>
          <h2>HomeRoute.tsx</h2>
          <p>All that is left to do now is apply our animation utility class to each of your routes.</p>
          <CodeSample language="tsx" code={codeSampleHomeRoute} />
          <p>This animation will execute as soon as your route component has been lazy loaded and mounted.</p>
          <p>I hope this helps somebody out - let me know on <ExternalLink className="u-anchor u-anchor--accent u-anchor--underline" href="https://twitter.com/romiem">Twitter (@romiem)</ExternalLink> if you found this helpful or are using a similar solution!</p>
        </ArticleText>

      </ArticleTemplate>

    </Page>
  );
};

const codeSampleApp =
  `export const App: React.FC = () => {
   const location = useLocation();
   return (
     <div className="App">
       <TransitionGroup>
         <CSSTransition
           key={location.pathname}
           classNames="fade"
           timeout={{ enter: 1000, exit: 200 }}>
           <Suspense fallback={<Loading />}>
             <Switch location={location}>
               <Route
                 path="/" exact
                 component={React.lazy(() => import('./Home/HomeRoute'))}
               />
               <Route
                 path="/test"
                 component={React.lazy(() => import('./Test/TestRoute'))}
               />
             </Switch>
           </Suspense>
         </CSSTransition>
       </TransitionGroup>
     </div>
   );
 };`;

const codeSampleStyles =
  `.a-routeFadeIn {
   animation: a-routeFadeIn 0.75s ease-in-out 0.25s;
   animation-fill-mode: both;
 }

 @keyframes a-routeFadeIn {
   0% {
     opacity: 0;
     transform: translateY(30px);
     height: 0;
     overflow: hidden;
   }
   0.01% {
     height: auto;
     overflow: visible;
   }
   100% {
     opacity: 1;
     transform: translateY(0);
   }
 }

 .a-routeFadeIn.fade-exit {
   animation: none;
   animation-fill-mode: none;
   opacity: 1;
   transition: opacity 0.2s ease-in-out;
 }

 .a-routeFadeIn.fade-exit-active {
   opacity: 0;
 }`;

const codeSampleHomeRoute =
  `export const HomeRoute: React.FC<RouteProps> = (props) => {
   return (
     <div className="HomeRoute a-routeFadeIn">
      <h1>Home</h1>
     </div>
   );
 };`;

export { AnimatingReactRouterRoute };
export default AnimatingReactRouterRoute;
